// check.js by Lian
// 

// 
const testPhoneNumber = phoneNumber =>
  /^1[3|4|5|7|8][0-9]{9}$/gi.test(phoneNumber);
//  :6~20(~!@#$%^&*()_+[]<>.-=)
const testPassword = str =>
  /^[A-Za-z0-9~!@#$%^&*\(\)_+\[\]<>.\-=]{6,20}$/gi.test(str);

const testPassword1 = str =>
  /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[~!@#$%^&*\(\)_+\[\]<>.\-=]).{6,20}$/gi.test(
    str
  );
//
const testValidCharacter = str =>
  /^[A-Za-z0-9~!@#$%^&*\(\)_+\[\]<>.\-=]{1,}$/gi.test(str);

//
const testFloatNum = str => /^-?\d+(\.\d{1,2})?$/gi.test(str);

// 
const testEmail = str => /^(\w+)(\.\w+)*@(\w+)(\.\w+)*\.[a-zA-Z]+$/gi.test(str);

// 
const testVerifyCode = str => /^[0-9]{6}$/gi.test(str);

// :2~6
const testUserName = str => /^[A-Za-z\u4e00-\u9fa5]{2,6}$/gi.test(str);

// :6~30(~!@#$%^&*()_+[]<>.-=)
const testAccountName = str =>
  /^[A-Za-z0-9~!@#$%^&*\(\)_+\[\]<>.\-=]{6,30}$/gi.test(str);

// :6~20
const testRoleName = str => /^[\u4e00-\u9fa5]{6,20}$/gi.test(str);

// :0-100
const testRoleDes = str => /^[\u4e00-\u9fa5]{0,100}$/gi.test(str);

// :2~20
const testMenuName = str => /^[\u4e00-\u9fa5]{2,20}$/gi.test(str);

// :0-100
const testMenuDes = str => /^[\u4e00-\u9fa5]{0,100}$/gi.test(str);

// 
const testWord = str => {
  return /^[a-z]+$/gi.test(str);
};

export {
  testWord,
  testVerifyCode,
  testPhoneNumber,
  testPassword,
  testValidCharacter,
  testEmail,
  testUserName,
  testAccountName,
  testRoleName,
  testRoleDes,
  testMenuName,
  testMenuDes,
  testFloatNum
};
